<template>
  <div class="app-container" id="alarmSettings">
    <div class="filter-container">
      <el-button class="filter-item" type="primary" @click="handleCreate" icon="el-icon-circle-plus-outline">添加</el-button>
    </div>
    <div class="tableBox">
      <el-table
        :data="list.slice((currpage - 1) * pagesize, currpage * pagesize)"
        @selection-change="handleSelectionChange"
        :key="tableKey"
        v-loading="listLoading"
        fit
        highlight-current-row
        style="width: 100%;"
        :row-class-name="tableRowClassName"
      >
        <el-table-column
          label="序号"
          prop="id"
          sortable="custom"
          align="center"
          width="80"
          type="index"
          :index="indexMethod"
        ></el-table-column>
        <el-table-column label="ID" width="150px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.id }}</span>
          </template>
        </el-table-column>
        <el-table-column label="名称" min-width="250px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.name}}</span>
          </template>
        </el-table-column>
        <el-table-column label="协议号" width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.protoNo }}</span>
          </template>
        </el-table-column>
        <el-table-column label="状态" width="200px" align="center">
          <template slot-scope="scope">
            <!-- <span :class="{'red':scope.row.enable==0}">{{ formateEnale( scope.row.enable )}}</span> -->
            <el-switch
              class="enableSwitch"
              v-model="scope.row.enable"
              active-color="#3C7EFE"
              inactive-color="#FF3F3F"
              :active-value="1"
              :inactive-value="0"
              @change="enableChange(scope.row)"
            ></el-switch>
          </template>
        </el-table-column>

        <el-table-column label="操作" align="center" min-width="230" fixed="right">
          <template slot-scope="{row}">
            <el-button type="primary" size="mini" @click="handleUpdate(row)" icon="el-icon-edit-outline">编辑</el-button>
            <el-button type="danger" size="mini" @click="handleDelete(row)" icon="el-icon-delete">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-pagination
      background
      layout="prev, pager, next, sizes, total, jumper"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pagesize"
      :total="total"
      @current-change="handleCurrentChange"
      @size-change="handleSizeChange"
    ></el-pagination>
    <el-dialog :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center>
      <el-form
        ref="dataForm"
        :rules="rules"
        :model="temp"
        label-position="left"
        class="formStyle"
      >
        <el-form-item label="协议号" prop="protoNo">
          <el-input v-model.number="temp.protoNo " />
        </el-form-item>
        <el-form-item label="名称" prop="name">
          <el-input v-model="temp.name " />
        </el-form-item>
        <el-form-item label="超速设置" v-if="dialogStatus==='update'&&temp.id==6">
          <el-input min="0" type="number" v-model.number="temp.speed"></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="enable" v-if="dialogStatus==='create'">
          <el-select v-model.number="temp.enable" class="filter-item" placeholder="Please select">
            <el-option
              v-for="item in enableOptions"
              :key="item.key"
              :label="item.display_name"
              :value="item.key"
            />
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogStatus==='create'?createData():updateData()">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import {
  protoList,
  protoHandler,
  protoDelete
} from "../../../api/alarmSetting"; //分别是协议列表 编辑协议 删除协议接口
import { setLimitSpeed, getlimitSpeed } from "../../../api/systemSettings"; //功能是设置超速值 和获取超速值（入口在设备电量低、超速等异常报警这个协议的编辑功能里面）

export default {
  name: "alarmSettings",
  components: { Pagination },
  data() {
    return {
      tableKey: 0,
      list: [], //协议列表(前端分页)
      total: 0, //协议总条数
      listLoading: true,
      pagesize: 10, //每页显示项数
      currpage: 1, //页码
      indexMethod: 1, //序号
      enableOptions: [
        { key: 1, display_name: "推送" },
        { key: 0, display_name: "禁止推送" }
      ],
      temp: {
        id: null,
        name: null,
        protoNo: null,
        enable: null,
        speed: 50
      }, //编辑后需要提交的内容
      speed1: null, //获取到的初始超速值
      dialogFormVisible: false,
      dialogStatus: "",
      textMap: {
        update: "编辑",
        create: "添加"
      }, //dialog显示的头部添加或编辑
      rules: {
        name: [{ required: true, message: "请填写报警名称", trigger: "blur" }],
        protoNo: [{ required: true, message: "请填写协议号", trigger: "blur" }],
        enable: [
          { required: true, message: "请选择推送状态", trigger: "change" }
        ],
        speed: [{ required: true, message: "请填写超速值", trigger: "blur" }]
      } //表单校验
    };
  },
  created() {
    this.getList();
  },
  methods: {
    //协议列表
    getList() {
      var that = this;
      protoList({}).then(res => {
        if (res.code == 200) {
          this.total = res.data.length;
          that.listLoading = false;
          that.list = res.data;
        } else {
          this.$message.error(res.msg);
        }
      });
      getlimitSpeed().then(res => {
        if (res.status == 0) {
          that.speed1 = res.data;
        }
      });
    },
    //删除协议
    handleDelete(row) {
      protoDelete({
        id: row.id
      }).then(res => {
        if (res.code == 200) {
          this.getList();
          this.$message({
            message: res.msg,
            type: "success"
          });
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    //重置
    resetTemp() {
      this.temp = {
        id: null,
        name: null,
        protoNo: null,
        enable: null
      };
    },
    //点击添加按钮
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
      //this.speed1是初始的时候获取的超速值，需要重新赋值给this.temp.speed的原因是由于点击添加的时候已经通过this.resetTemp()把this.temp.speed清空，目的是在继续进行编辑超速值操作时也能获取到初始超速值
      this.temp.speed = this.speed1;
    },
    //添加协议
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          protoHandler({
            name: this.temp.name,
            protoNo: this.temp.protoNo,
            enable: this.temp.enable
          }).then(res => {
            if (res.code == 200) {
              this.getList();
              this.dialogFormVisible = false;
              this.$message({
                message: res.msg,
                type: "success"
              });
            } else {
              this.$message.error(res.msg);
            }
          });
        } else {
          return false;
        }
      });
    },
    //点击编辑按钮
    handleUpdate(row) {
      row.speed = this.speed1;
      this.temp = Object.assign({}, row); // copy obj
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //编辑
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          let speed = this.temp.speed;
          protoHandler(this.temp).then(res => {
            if (res.code == 200) {
              this.getList();
              this.dialogFormVisible = false;
              this.$message({
                message: res.msg,
                type: "success"
              });
            } else {
              this.$message.error(res.msg);
            }
          });
          //如果时编辑的设备电量低、超速等异常报警这个协议的话会涉及到超速报警的报警速度设置
          if (typeof speed == "number" && this.temp.id == 6) {
            setLimitSpeed({ speed: speed }).then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
              }
            });
          } else {
            this.$message.warning("超速值请输入数字");
          }
        } else {
          return false;
        }
      });
    },
    //关闭或打开这个推送（推送或禁止推送）
    enableChange(row) {
      this.temp = Object.assign({}, row);
      protoHandler(this.temp).then(res => {
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            type: "success"
          });
        } else {
          this.$message.error(res.msg);
        }
      });
    },

    //显示推送或禁止推送
    formateEnale(data) {
      if (data == 1) {
        return "推送";
      } else {
        return "禁止推送";
      }
    },
    //跳转页码
    handleCurrentChange(cpage) {
      this.currpage = cpage;
      this.indexMethod = (this.currpage - 1) * this.pagesize + 1;
    },
    //每页展示条数
    handleSizeChange(psize) {
      this.pagesize = psize;
      this.indexMethod = (this.currpage - 1) * this.pagesize + 1;
    },
    handleSelectionChange(val) {},
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    }
  }
};
</script>
<style lang="scss">
#alarmSettings {
}
.red {
  color: red;
}
</style>









