import request from '../request'
//车辆超速设置
export function setLimitSpeed(data) {
    return request({
        url: '/cars/limitSpeed',
        method: 'post',
        data
    })
}

//车辆超速设置获取
export function getlimitSpeed(data) {
    return request({
        url: '/cars/getlimitSpeed',
        method: 'post',
        data
    })
}

//开启关闭枪支定位算法
export function gunOptimize(data) {
    return request({
        url: '/params/gunOptimize',
        method: 'post',
        data
    })
}

