import request from '../request'

//获取所有大队下的所有车辆(包括没绑定设备的车辆)
export function teamAndCarList(data) {
    return request({
        url: '/cars/allList',
        method: 'post',
        data
    })
}

export function teamAndCarAllBindList(data) {
    return request({
        url: '/cars/allBindList',
        method: 'post',
        data
    })
}

//获取所有大队下的所有枪支
export function teamAndGunList(data) {
    return request({
        url: '/guns/allList',
        method: 'post',
        data
    })
}
//延迟一条异常报警
export function logsDelay(data) {   
    return request({
        url: '/logs/delays',
        method: 'post',
        data
    })
}
//获取枪支设备最后点信息
export function lastgps(data) {
    return request({
        url: '/map/lastgps',
        method: 'post',
        // params:data,
        data,
        headers:{'content-type':'application/json'},
    })
}
// 车辆最新位置信息
export function vehicleStatus(data) {
    return request({
        url: '/proxy/StandardApiAction_vehicleStatus',
        method: 'post',
        data
    })
}

