import request from '../../request'
//车辆列表
export function carlist(data) {
    return request({
        url: '/cars/list',
        method: 'post',
        data
    })
}
//添加车辆
export function carAdd(data) {
    return request({
        url: '/cars/add',
        method: 'post',
        data

    })
}
//删除车辆
export function cardelete(data) {
    return request({
        url: '/cars/delete',
        method: 'post',
        data
    })
}
//编辑车辆信息
export function carupdate(data) {
    return request({
        url: '/cars/update',
        method: 'post',
        data
    })
}
//根据车牌号搜索车辆
export function carSearch(data) {
    return request({
        url: '/cars/query',
        method: 'post',
        data
    })
}
//批量添加
export function addBatch(data) {
    return request({
        url: '/cars/addBatch',
        method: 'post',
        data
    })
}


//设置设备暂停报警时间
export function setPause(data) {
    return request({
        url: '/params/setPause',
        method: 'post',
        data,
        headers:{'content-type':'application/json'}
    })
}
//报废设备
export function discardCar(data) {
    return request({
        url: '/cars/scrap',
        method: 'post',
        data,
    })
}

