import request from '../request'
//报警协议列表
export function protoList(data) {
    return request({
        url: '/proto/list',
        method: 'post',
        data
    })
}
//添加和修改报警协议
export function protoHandler(data) {
    return request({
        url: '/proto/handler',
        method: 'post',
        data,
        headers:{'content-type':'application/json'}
    })
}

//删除报警协议
export function protoDelete(data) {
    return request({
        url: '/proto/del',
        method: 'post',
        data
    })
}
