import request from '../request'
//获取被报警屏蔽的所有设备
export function queryPause(data) {
    return request({
        url: '/params/queryPause',
        method: 'post',
        data
    })
}
//获取暂停拆卸报警的设备
export function getPause(data) {
    return request({
        url: '/broken/getPause',
        method: 'post',
        data
    })
}
//设置设备暂停报警时间  
export function setPauseBroken(data) {
    return request({
        url: '/broken/setPause',
        method: 'post',
        data
    })
}
//取消暂停拆卸报警
export function cancelPause(data) {
    return request({
        url: '/broken/cancelPause',
        method: 'post',
        data
    })
}

