<template>
  <div class="app-container" id="systemSettings">
    <div class="parkForm">
      <el-form ref="parkForm" :inline="true" :rules="parkRules" :model="parkForm" label-width="250px">
        <el-form-item label="停车报警阈值(分钟)" prop="parkTime">
          <el-input min="0" type="number" v-model.number="parkForm.parkTime"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitParkTime">设置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <el-form ref="speedForm" :inline="true" :rules="speedRules" :model="speedForm" label-width="250px">
      <el-form-item label="超速报警阈值设置" prop="speed">
        <el-input min="0" type="number" v-model.number="speedForm.speed"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitSpeed">设置</el-button>
      </el-form-item>
    </el-form>
    <el-form ref="form" :model="form" label-width="250px" class="positon" v-if="power==1">
      <el-form-item label="枪支定位算法">
        <el-switch :active-value="true" :inactive-value="false" v-model="form.enabled" @change="changeGunOptimize"></el-switch>
      </el-form-item>
    </el-form>
    <!-- <el-form ref="numForm" :model="numForm" :inline="true" label-width="250px">
      <el-form-item label="实时监控设备查看上限">
        <el-select v-model="deviceNum" placeholder="请选择">
          <el-option
            v-for="item in numOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitDeviceNum">设置</el-button>
      </el-form-item>
    </el-form> -->
    <el-form :model="sendForm" ref="ruleForm" label-width="100px" class="sendForm" v-if="power==1">
      <!-- <div class="text">向所有设备发送命令：</div> -->
      <el-form-item required label="向所有设备发送命令：" label-width="250px">
        <el-col :span="4">
          <el-form-item prop="timeAll">
            <el-input min="1" max="255" type="number" placeholder="请填写时间" v-model.number="sendForm.timeAll"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item>
            <el-button type="primary" @click="sendAllClick">下发</el-button>
          </el-form-item>
        </el-col>
      </el-form-item>
      <!-- <div class="text">向指定设备发送命令(枪支设备)：</div> -->
      <el-form-item required label="向指定设备发送命令(枪支设备)：" label-width="250px">
        <el-col :span="4">
          <el-form-item prop="timeOne">
            <el-input min="1" max="255" placeholder="请填写时间" type="number" v-model.number="sendForm.timeOne"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item prop="imei">
            <el-input placeholder="请填写设备号" v-model="sendForm.imei"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item>
            <el-button type="primary" @click="sendOneClick">下发</el-button>
          </el-form-item>
        </el-col>
      </el-form-item>
    </el-form>
    <el-form ref="form" :inline="true" :model="gunAlarmForm" label-width="250px">
      <!-- <el-form-item label="一键取消归还时间报警"  label-width="250px">
    <el-switch v-model="gunAlarmForm.beCancel"></el-switch>
  </el-form-item> -->
      <el-form-item label="枪支归还时间">
        <el-time-picker v-model="gunAlarmForm.returnTime" placeholder="枪支归还时间" :clearable="false" value-format="HH:mm:ss">
        </el-time-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="returnTimeSubmit">设置时间</el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="beCancelSubmit">取消枪支归还时间</el-button>
      </el-form-item>
    </el-form>
  </div>

  <!-- </div> -->
</template>

<script>

import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
//设置超速值 获取超速值 枪支定位算法打开或关闭 获取所有系统配置参数 向所有设备下发 向一个设备下发 设置实时监控显示上限 获取实时监控显示上限
import {
  setLimitSpeed,
  getlimitSpeed,
  gunOptimize,
  getParamsValues,
  sendAll,
  sendOne,
  getDisplayLimit,
  setDisplayLimit,
  setOffline,
  queryOffline,
  updateAllReturnTime
} from "../../../api/systemSettings";
const speedValidate = (rule, value, callback) => {
  if (value == "" || value == undefined) {
    callback(new Error("请填写超速阈值"));
  } else if (!(/(^[1-9]\d*$)/.test(value))) {
    callback(new Error("请填写正整数"));
  } else {
    callback()
  }
};
export default {
  name: "systemSettings",
  components: { Pagination },

  data() {
    return {
      form: {
        enabled: null //枪支定位算法开关
      },
      sendForm: {
        timeAll: undefined,
        timeOne: undefined,
        imei: undefined
      },//下发数据
      deviceNum: 20,//默认实时监控显示设备上限
      numForm: {},
      numOptions: [],//实时监控可选显示设备上限数
      parkRules: {
        parkTime: [{ required: true, message: "请填写停车报警阈值", trigger: "blur" }],

      },
      power: JSON.parse(this.$store.state.user.userInfo).Power,
      parkForm: {
        parkTime: null
      },
      speedForm: {
        speed: undefined
      },
      speedRules: {
        speed: [{ required: true, message: "请填写超速报警阈值", trigger: "blur", validator: speedValidate }],

      },
      gunAlarmForm: {
        beCancel: false,
        returnTime: ''
      }
    };
  },
  created() {
    if (this.power == 1) {
      //初始化实时监控设备查看上限的下拉框
      this.formatNumOptions();

      //初始化枪支定位算法开关
      getParamsValues().then(res => {
        if (res.status == 0) {
          this.form.enabled = res.data.gunOptimize;
        }
      });
      //初始化获取实时监控设备查看上限
      getDisplayLimit().then(res => {
        if (res.status == 0) {
          this.deviceNum = res.data.limit;
        }
      });
    }

    this.getPark();
    this.getSpeed();
  },
  methods: {
    returnTimeSubmit() {
      if (!this.gunAlarmForm.returnTime) {
        this.$message.warning('请选择枪支归还时间')
        return;
      }
      updateAllReturnTime({ beCancel: true, returnTime: this.gunAlarmForm.returnTime }).then(res => {
        if (res.status == 0) {
          this.$message.success(res.msg)
        }
      });
    },
    beCancelSubmit() {
      updateAllReturnTime({ beCancel: false }).then(res => {
        if (res.status == 0) {
          this.$message.success(res.msg)
        }
      });
    },
    getPark() {
      queryOffline().then(res => {
        if (res.status == 0) {
          this.parkForm.parkTime = res.data / 60;
        }
      });
    },
    //协议列表
    getSpeed() {
      var that = this;
      getlimitSpeed().then(res => {
        if (res.status == 0) {
          that.speedForm.speed = res.data;
        }
      });
    },
    submitParkTime() {
      this.$refs["parkForm"].validate(valid => {
        if (valid) {
          setOffline({ time: this.parkForm.parkTime * 60 }).then(res => {
            if (res.status == 0) {
              this.$message.success(res.msg)
              this.getSpeed();
            }
          });
        }
      })
    },
    submitSpeed() {
      this.$refs["speedForm"].validate(valid => {
        if (valid) {
          setLimitSpeed({ speed: this.speedForm.speed }).then(res => {
            if (res.status == 0) {
              this.getList();
              this.$message.success(res.msg);
            }
          });
        }
      })
    },
    //枪支定位算法打开关闭
    changeGunOptimize() {
      let enabled = this.form.enabled;

      gunOptimize({ enabled: enabled }).then(res => {
        if (res.status == 0) {
          this.$message.success(res.msg);
        }
      });
    },
    //向所以设备下发
    sendAllClick() {
      let time = this.sendForm.timeAll;
      if (!time) {
        this.$message.warning("请输入时间");
      }
      sendAll({ time: time }).then(res => {
        if (res.status == 0) {
          this.$message.success(res.msg);
        }
      });
    },
    //向指定设备下发
    sendOneClick() {
      let time = this.sendForm.timeOne;
      let imei = this.sendForm.imei;
      if (!time || !imei) {
        this.$message.warning("请输入信息");
      }
      sendOne({ time: time, imei: imei }).then(res => {
        if (res.status == 0) {
          this.$message.success(res.msg);
        }
      });
    },
    //格式化实时监控设备查看上限的下拉框数据
    formatNumOptions() {
      for (let i = 5; i < 101; i++) {
        this.numOptions.push({
          value: i,
          label: i
        });
      }
    },
    //实时监控设备查看上限
    submitDeviceNum() {
      setDisplayLimit({ limit: this.deviceNum }).then(res => {
        if (res.status == 0) {
          this.$message.success(res.msg);
        }
      });
    }
  }
};
</script>
<style lang="scss">
#systemSettings {
  .parkForm {
    padding-top: 20px;
  }
  .text {
    font-size: 14px;
    color: #606266;
    font-weight: 700;
    margin-bottom: 15px;
  }
}
.red {
  color: red;
}

.speedInput {
  width: 250px;
}
