<template>
  <div class="app-container" id="alarmScreenSettings">
    <!-- 报警屏蔽 -->
    <div class="multidevice filter-container" v-loading="mdLoad">
      <!-- 查找面板 -->
      <div class="selectBox">
        <el-form
          :inline="true"
          class="mdElForm"
          :rules="mdRule"
          ref="mdForm"
          :model="mdForm"
          label-width="80px"
        >
          <el-form-item class="pauseSelect" v-if="power==1">
            <el-select
              v-model="pauseType"
              class="filter-item"
              @change="pauseTypeChange"
              placeholder="请选择屏蔽类型"
            >
              <el-option
                v-for="item in pauseTypeList"
                :key="item.type"
                :label="item.option"
                :value="item.type"
              />
            </el-select>
          </el-form-item>
          <div class="telSearch">
            <div
              class="radioBtn"
              v-if="pauseType==1"
              :class="{'hasColor':(telChoose=='1')}"
              @click="telChoose='1'"
            >车辆</div>
            <div
              class="radioBtn"
              v-if="pauseType==1"
              :class="{'hasColor':(telChoose=='2')}"
              @click="telChoose='2'"
            >枪支</div>
          </div>
          <el-form-item v-if="pauseType==2" class="pauseSelect">
            <el-select
              v-model="pauseVal"
              class="filter-item"
              @change="pauseTypeChange"
              placeholder="暂停或取消暂停"
            >
              <el-option
                v-for="item in pauseValList"
                :key="item.type"
                :label="item.option"
                :value="item.type"
              />
            </el-select>
          </el-form-item>
          <el-form-item prop="imeiCar" v-show="telChoose=='1'&&pauseType==1">
            <el-cascader
              @change="removeValid()"
              v-model="mdForm.imeiCar"
              placeholder="选择车辆"
              :props="propsCar"
              :options="dataCar"
              :show-all-levels="false"
              clearable
              filterable
              collapse-tags
            >
              <template slot-scope="{ node, data }">
                <span>{{ data.label }}</span>
                <span v-if="!node.isLeaf">({{ data.children.length }})</span>
                <span
                  v-if="node.isLeaf &&data.disabled1==true"
                >({{data.time?data.time+'分钟':"小于1分钟"}})</span>
              </template>
            </el-cascader>
          </el-form-item>

          <el-form-item prop="imeiGun" v-show="!(telChoose=='1'&&pauseType==1)">
            <!-- <el-cascader
              popper-class="myGunCas"
              ref="myMdGunsCas"
              @change="removeValid()"
              v-model="mdForm.imeiGun"
              placeholder="选择枪支"
              :props="{expandTrigger:'hover',multiple:true}"
              :options="dataGun"
              :show-all-levels="false"
              clearable
              filterable
              collapse-tags
            > -->
              <el-cascader
               popper-class="myGunCas"
                ref="myMdGunsCas"
                @change="removeValid()"
               
                  
                   v-model="mdForm.imeiGun"
                    placeholder="选择枪支"
                     :props="{expandTrigger:'hover',emitPath:false,multiple:true}"
                      :options="dataGun"
                       :show-all-levels="false" 
                       clearable 
                       filterable>
                 
              <template slot-scope="{ node, data }">
                <span>{{ data.label }}</span>
                <span v-if="!node.isLeaf">({{ data.children.length }})</span>
                <span
                  v-if="node.isLeaf &&data.disabled1==true&&pauseType==1"
                >({{data.time?data.time+'分钟':"小于1分钟"}})</span>
                <span v-if="node.isLeaf &&data.disabled1==true&&pauseType==2">(暂停中)</span>
              </template>
            </el-cascader>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="pauseAlarm" icon="iconfont iconshezhi">设置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="tableBox">
      <el-table
        :data="pauseData.slice((currpage - 1) * pagesize, currpage * pagesize)"
        :key="tableKey"
        border
        fit
        highlight-current-row
        style="width: 100%;"
        :row-class-name="tableRowClassName"
      >
        <el-table-column
          label="序号"
          fixed
          prop="id"
          sortable="custom"
          align="center"
          width="80"
          type="index"
        ></el-table-column>
        <el-table-column label="车牌号或枪号" width="400px" align="center">
          <template slot-scope="{row}">
            <span>{{ $store.state.realTimeMonitor.carAndGunObj[row.imei]?$store.state.realTimeMonitor.carAndGunObj[row.imei].code:'' }}</span>
          </template>
        </el-table-column>
        <el-table-column label="设备号" width="400px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.imei }}</span>
          </template>
        </el-table-column>

        <el-table-column
          label="剩余暂停报警时间(分钟)"
          align="center"
          min-width="300"
          fixed="right"
          class-name="small-padding fixed-width"
          v-if="pauseType==1"
        >
          <template slot-scope="{row}">
            <span>{{ row.time==0?'小于1分钟':row.time}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="状态"
          align="center"
          min-width="300"
          fixed="right"
          class-name="small-padding fixed-width"
          v-if="pauseType==2"
        >
          <template slot-scope="{row}">
            <span>暂停</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      layout="prev, pager, next, sizes, total, jumper"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pagesize"
      :total="pauseData.length"
      :current-page="currpage"
      @current-change="handleCurrentChange"
      @size-change="handleSizeChange"
    ></el-pagination>
    <el-dialog
      id="myDialog"
      class="pauseForm"
      title="暂停报警"
      :visible.sync="dialogVisibleOfPause"
      center
    >
      <el-form ref="pauseForm" :rules="rules2" :model="pausetemp" label-position="left">
        <!-- <el-form-item label="车牌号" prop="licenseNo">
          <el-input v-model="addAlltemp.licenseNo" />
        </el-form-item>-->
        <el-form-item label="暂停时间" prop="time">
          <el-input v-model.number="pausetemp.time"  placeholder="单位（分钟）" type="number" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="pausedSubmit()">确定</el-button>
      </div>
    </el-dialog>
    <!-- end -->
  </div>
</template>

<script>
import {
  teamAndCarAllBindList,
  teamAndGunList
} from "../../../api/realTimeMonitoring";
import {
  queryPause,
  getPause,
  setPauseBroken,
  cancelPause
} from "../../../api/alarmSreenSettings";
import { setPause } from "../../../api/managementArchives/vehicleArchives";
export default {
  name: "alarmScreenSettings",

  data() {
    let multiRule = (rule, value, callback) => {
      // 多设备验证
      let cars = this.mdForm.imeiCar,
        guns = this.mdForm.imeiGun;
      if (cars.length < 1 && guns.length < 1) {
        callback(new Error("请选择设备"));
      } else {
        callback();
      }
    };
    return {
      tableKey: 1,
      telChoose: "1",
      power:2,
      mdLoad: false, // 加载动画
      mdForm: {
        // 多设备查询
        imeiCar: [],
        imeiGun: []
      },
      mdRule: {
        // 多设备验证
        imeiCar: [{ validator: multiRule, trigger: "change" }],
        imeiGun: [{ validator: multiRule, trigger: "change" }]
      },
      propsCar: {
        expandTrigger: "hover",
        multiple: true
      },
      propsGun: {
        expandTrigger: "hover",
        multiple: true
      },
      dataCar: [], // 多设备车
      dataGun: [], // 多设备枪,
      imei2name: {}, // 设备的imei和对应的名字
      pausedDevice: {},
      dialogVisibleOfPause: false,
      pausetemp: {
        time: null
      },
      pauseData: [],
      rules2: {
        time: [
          {
            required: true,
            message: "请填写暂停时间",
            trigger: "blur"
          },
          { message: '暂停时间需大于0', trigger: 'blur' ,validator:(rule,value,callback)=>{
    if(value<=0){callback(new Error('时间需大于0'))
    }else{
        callback()
    }
}}
        ]
      },
      currpage: 1,
      pagesize: 10,
      pauseType: 1,
      pauseTypeList: [
        { type: 1, option: "报警屏蔽" },
        { type: 2, option: "拆卸报警暂停" }
      ],
      pauseVal: 1,
      pauseValList: [
        { type: 1, option: "暂停拆卸报警" },
        { type: 2, option: "取消暂停拆卸报警" }
      ]
    };
  },
  created() {
    this.getPausedDevice();
    this.power = JSON.parse(this.$store.state.user.userInfo).Power;
  },
  methods: {
   
    getList() {},

    removeValid() {
      // 选择设备后移除验证
      this.$refs.mdForm.clearValidate(["imeiCar", "imeiGun"]);
    },
    //获取左侧大队和车辆tree请求
    getTeamAndCarList() {
      let that = this;
      teamAndCarAllBindList({}).then(res => {
        if (res.status == 0) {
          that.dataCar = this.formatCas(res.data, "serialNo", "licenseNo"); // 设置车下拉树的数据格式
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    // 获取枪支及大队列表
    getTeamAndGunList() {
      let that = this;
      teamAndGunList({}).then(res => {
        if (res.status == 0) {
          that.dataGun = this.formatCas(res.data, "iMei", "code"); // 设置枪下拉树的数据格式
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    formatCas(data, imei, name) {
      // 格式化级联选择器数据 data是原始数据 imei是vaue值 name是label值
      let result = [],
        id = 0,
        _t = this;
      for (let i in data) {
        let childs = []; // 格式化子数组
        if(i=="报废车辆"){continue}
         if(i=="报废枪支"){continue}
        
        data[i].forEach(x => {
          //过滤掉没有绑定设备的车
          if(x[imei]!==""){
          let disabled = false;
          let time = 0;
          
          if (this.pausedDevice.hasOwnProperty(x[imei])) {
            disabled = true;
            time = this.pausedDevice[x[imei]];
          }

          let disabled2 = disabled;
          //如果不是暂停或取消拆卸报警,都是不禁用的
          if (this.pauseType == 1) {
            disabled2 = false;
          } else {
            //如果是暂停或取消拆卸报警,判断是禁用暂停的还禁用没暂停的设备
            if (this.pauseVal == 2) {
              disabled2 = !disabled;
            }
          }
          
          childs.push({
            value: x[imei],
            label: x[name],
            disabled1: disabled,
            disabled: disabled2,
            time: time
          });
          _t.imei2name[x[imei]] = x[name]; // 将设备号与设备名组成一个数组
          }
        });
        result.push({
          value: `i${id++}`,
          label: i == "" ? "未分组" : i,
          children: childs
        });
      }
      return result;
    },
    getPausedDevice() {
      let that = this;
      if (this.pauseType == 1) {
        queryPause({}).then(res => {
          if (res.status == 0) {
            this.pausedDevice = {};
            this.pauseData = res.data;
            for (let i = 0; i < res.data.length; i++) {
              this.pausedDevice[res.data[i].imei] = res.data[i].time;
            }
            this.getTeamAndCarList();
            this.getTeamAndGunList();
          } else {
            this.$message.error(res.msg);
          }
        });
      } else {
        getPause({}).then(res => {
          if (res.status == 0) {
            this.pausedDevice = {};
            this.pauseData = [];
            for (let i = 0; i < res.data.length; i++) {
              this.pausedDevice[res.data[i]] = null;
              this.pauseData.push({ imei: res.data[i] });
            }

            // this.getTeamAndCarList();
            this.getTeamAndGunList();
          } else {
            this.$message.error(res.msg);
          }
        });
      }
    },
    pauseAlarm() {
      let _t = this;
      _t.$refs["mdForm"].validate(valid => {
        if (valid) {
          //如果是暂停拆卸报警或者是取消暂停拆卸报警
          if (this.pauseType == 2) {
            //暂停拆卸报警

            if (this.pauseVal == 1) {
              this.pauseBroken();
            } else {
              //取消暂停拆卸报警
              this.cancleBroken();
            }
            return;
          }
          this.pausetemp.time = null;

          this.dialogVisibleOfPause = true;

          this.$nextTick(() => {
            this.$refs["pauseForm"].clearValidate();
          });
        }
      });
    },
    changeValue() {
      // 切换设备时清空值
      this.mdForm.imeiCar = [];
      this.mdForm.imeiGun = [];
    },
    pauseBroken() {
      let carAndGun = this.mdForm.imeiGun;
      let submitArr = [];
      for (let i = 0; i < carAndGun.length; i++) {
        submitArr.push(carAndGun[i][1]);
      }
      setPauseBroken({ imeis: submitArr })
        .then(res => {
          if (res.status == 0) {
            if (res.success == false) {
              this.$message.error(res.msg);
              return;
            }
            this.changeValue();
            this.getPausedDevice();

            this.$message.success("设置成功");
            this.dialogVisibleOfPause = false;
            this.$nextTick(() => {
              this.$refs["mdForm"].clearValidate();
            });
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          //   myLoading.close();
        });
    },
    cancleBroken() {
      let carAndGun = this.mdForm.imeiGun;
      let submitArr = [];
      for (let i = 0; i < carAndGun.length; i++) {
        submitArr.push(carAndGun[i][1]);
      }
      cancelPause({ imeis: submitArr })
        .then(res => {
          if (res.status == 0) {
            if (res.success == false) {
              this.$message.error(res.msg);
              return;
            }
            this.changeValue();
            this.getPausedDevice();

            this.$message.success("设置成功");
            this.dialogVisibleOfPause = false;
            this.$nextTick(() => {
              this.$refs["mdForm"].clearValidate();
            });
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          //   myLoading.close();
        });
    },
    pausedSubmit() {
      //选择的暂停拆卸报警
      this.$refs["pauseForm"].validate(valid => {
        if (valid) {
          //   myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          let carAndGun = this.mdForm.imeiCar.concat(this.mdForm.imeiGun);
          let submitArr = [];
          for (let i = 0; i < carAndGun.length; i++) {
            submitArr.push({
              imei: carAndGun[i][1],
              time: this.pausetemp.time
            });
          }
          setPause(JSON.stringify(submitArr))
            .then(res => {
              if (res.status == 0) {
                this.changeValue();
                this.getPausedDevice();

                this.$message.success("设置成功");
                this.dialogVisibleOfPause = false;
                this.$nextTick(() => {
                  this.$refs["mdForm"].clearValidate();
                });
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              //   myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    handleCurrentChange(cpage) {
      this.currpage = cpage;
    },
    handleSizeChange(psize) {
      this.pagesize = psize;
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    pauseTypeChange(val) {
      this.getPausedDevice();
    }
  }
};
</script>
<style lang="scss">
#alarmScreenSettings {
  .filter-container {
    height: 70px;
  }
  .selectBox {
    display: flex;
    align-items: center;
    .pauseSelect {
      width: 165px !important;
      .el-input__inner,
      .el-form-item__content,
      .el-cascader,
      .el-input,
      .el-select,
      .el-form-item {
        width: 165px !important;
      }
    }
    .el-select {
      width: 300px;
    }
    .el-input__inner,
    .el-form-item__content,
    .el-cascader,
    .el-input {
      width: 300px;
      height: 30px !important;
      line-height: 30px;
    }
    .el-cascader__search-input {
      background-color: #f9f9f9;
    }
    .el-form-item {
      margin-bottom: 0;
      width: 300px;
    }
  }
  .radioBtn {
    width: 83px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    margin-right: 20px;
    border-radius: 4px;
    background: rgba(250, 250, 250, 1);
    font-size: 12px;
    font-weight: bold;
    color: rgba(153, 153, 153, 1);
    cursor: pointer;
  }

  .hasColor {
    background: rgba(234, 245, 255, 1);
  }

  .telSearch {
    display: inline-flex;
  }
  .el-form-item__error {
    left: 14px;
  }
}
</style>









